<template>
  <div class="bmlist">
    <div v-for="bm of cur_cato_bms" :key="bm.id" class="bm_item">
      <div class="logo_wrapper">
        <img :src="bm.pic_url" alt="">
      </div>
      <button class="close_btn" @click="removeBm(bm.id)">close</button>
      <a :href="bm.href" target="_blank">{{bm.name}}</a>
    </div>
    <div class="add_bm" @click="toggleShow(true)">
      <img src="https://s1.ax1x.com/2022/07/18/joIsDf.png">
    </div>
    <Transition name="CShow">
      <AddBookMark v-show="showAddBmFlag" @toggleShow="toggleShow" :bmcatoId="$route.query.id" />
    </Transition>
  </div>
</template>

<script>
import AddBookMark from '../components/AddBookMark'

export default {
  name: 'BookMarkBoard',
  data() {
    return {
      cur_cato_bms: [],
      showAddBmFlag: false,
    }
  },
  methods: {
    updBMs() {
      if (this.$route.query.id === undefined) {
        // Ê≤°ÊúâÊåáÊòéid
        // ËøôÁßçÊÉÖÂÜµÂá∫Áé∞ÊòØÁõ¥Êé•ÁÇπÂáªÈ°∂ÈÉ®‚Äú‰π¶Á≠æüîñ‚ÄùÔºåÁî±‰∫éË∑ØÁî±ËßÑÂàôÊ≤°ÊúâÊåáÊòéÂÖ∑‰ΩìÁöÑidÊâÄ‰ª•ÂØºËá¥Êó†Ê≥ï‰ΩøÁî®'gotBMsById'
        this.$store.dispatch('gotBMs').then(({bookmarks}) => {
          this.cur_cato_bms = bookmarks
        })
        return 
      }
      this.$store.dispatch('gotBMsById', this.$route.query.id).then(({bookmarks}) => {
        this.cur_cato_bms = bookmarks
      })
    },
    toggleShow(tobeShown) {
      this.showAddBmFlag = tobeShown
    },
    removeBm(bmId) {
      if (window.confirm('Á°ÆËÆ§Âà†Èô§ËØ•‰π¶Á≠æ?')) {
        const catoId = this.$route.query.id
        this.$store.commit('RM_BM', {
          catoId,
          bmId
        })
        this.$router.push({
          path: '/'
        })
      }
    }
  },
  mounted() {
    this.updBMs()
    // Â∞ÜÊï∞ÊçÆÂ≠òÂà∞localstorage
    localStorage.setItem('bm', JSON.stringify(this.$store.state.bm))
  },
  beforeUpdate() {
    // console.log('upd', this.$route.query.id)
    this.updBMs()
  },
  components: {
    AddBookMark
  }
}
</script>

<style scoped>
.CShow-enter-active {
  animation: popon 0.2s ease-out;
}
.CShow-leave-active {
  animation: popon 0.2s reverse ease-in;
}
@keyframes popon {
  from {
    transform: translateY(-5%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.bmlist {
  width: 70vw;
  background-color: rgba(255, 255, 255, 0.8);
  margin: 0 auto;
  padding: 2em;
  display: flex;
  flex-wrap: wrap;
}
.bmlist .bm_item {
  margin: 1vw;
  /* ‰∏ÉÂàÜ‰πã‰∏ÄÂÆΩÂ∫¶ÂáèÂéªÂ∑¶Âè≥Èó¥Ë∑ù */
  width: 8vw;
  height: 11vw;
  text-align: center;
  transition: 0.5s ease;
  position: relative;
}
.bmlist .bm_item a {
  text-decoration: none;
  color: rgb(58, 56, 56);
}
.bmlist .bm_item:hover {
  transform: translateY(-10%);
  box-shadow: 0 1em 1.5em #ccc;
}
.bmlist .bm_item:hover .close_btn {
  /* Èº†Ê†áhoverÊâçÊòæÁ§∫Âà†Èô§ÊåâÈíÆ */
  display: block;
}
.bmlist .bm_item .logo_wrapper {
  width: 8vw;
  height: 8vw;
  line-height: 8vw;
}
.bmlist .bm_item .logo_wrapper img {
  width: 80%;
  vertical-align: middle;
}
.add_bm {
  width: 8vw;
  height: 11vw;
  line-height: 9vw;
  text-align: center;
  margin: 1vw;
}
.add_bm:hover {
  transform: translateY(-10%);
  box-shadow: 0 1em 1.5em #ccc;
  transition: 0.5s ease;
}
.add_bm img {
  width: 80%;
  vertical-align: middle;
}
.close_btn {
  position: absolute;
  z-index: 1;
  top: 0.3em;
  right: 0.3em;
  cursor: pointer;
  /* ÊåâÈíÆÊòØ‰∏™Â∞èÊ°Ü */
  width: 1.5em;
  height: 1.5em;
  border: 0;
  /* ÊääÊåâÈíÆÈáåÈù¢ÁöÑÂ≠óÊå§Âá∫ÂéªÂπ∂ÈöêËóè */
  text-indent: 10em;
  overflow: hidden;
  display: none;
}
.close_btn::after {
  position: absolute;
  z-index: 1;
  line-height: 0.5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-indent: 0;
  font-size: 1.5rem;
  content: '\00D7';
}
</style>
