<template>
  <div class="add_bm_mask">
    <div class="add_bm">
      <div class="add_bm_main">
        <span>ÁΩëÁ´ôÂêçÁß∞Ôºö</span><br><input type="text" v-model="new_bm_name"><br>
        <span>ÁΩëÂùÄÔºö</span><br><input type="text" v-model="new_bm_url"><br>
        <span>logoÈìæÊé•:</span><br><input type="text" v-model="new_bm_pic_url">
      </div>
      <button class="close_btn" @click="closeMe">close</button>
      <div class="add_bm_foot">
        <button @click="closeMe" class="btn_warn">ÂèñÊ∂à</button>
        <button @click="addOneBm" class="btn_sure">Á°ÆÂÆö</button>
      </div>
    </div>
  </div>
</template>

<script>
import { nanoid } from 'nanoid'
import { isURL } from '../utils/validation'

export default {
  name: 'AddBookMark',
  emits: ['toggleShow'],
  props: ['bmcatoId'],
  data() {
    return {
      new_bm_name: '',
      new_bm_url: '',
      new_bm_pic_url: '',
    }
  },
  computed: {
    isValid() {
      return this.new_bm_name.trim() !== '' &&
              isURL(this.new_bm_url) && isURL(this.new_bm_pic_url)
    }
  },
  methods: {
    closeMe(){
      this.$emit('toggleShow', false)
    },
    addOneBm() {
      if (!this.isValid) {
        return alert('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÊï∞ÊçÆ')
      }
      const info = {
        catoid: this.bmcatoId,
        bmItem: {
          id: nanoid(),
          name: this.new_bm_name,
          href: this.new_bm_url,
          pic_url: this.new_bm_pic_url
        }
      }
      this.$store.commit('ADD_BM', info)
      localStorage.setItem('bm', JSON.stringify(this.$store.state.bm))
      this.closeMe()
    }
  },
}
</script>

<style scoped>
.add_bm_mask {
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.add_bm {
  width: 30vw;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 0 0 0.8em 0px rgb(193 193 193);
  border-radius: 0.4em;

  position: fixed;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  justify-content: space-around;

  /* ËÆ©Â≠êÂÖÉÁ¥†ÂíåËæπÊ°ÜÊúâË∑ùÁ¶ª */
  padding: 1em;
}
.add_bm .add_bm_main {
  padding: 1em 0;
}
.add_bm .add_bm_main span {
  font-size: 0.8rem;
}
.add_bm .add_bm_main input {
  width: 76%;
  margin-left: 12%;
  margin-top: 1em;
  padding: 0.4em 0;
}
.add_bm .add_bm_foot {
  display: flex;
  justify-content: flex-end;
}
.add_bm .add_bm_foot button {
  padding: 0.5em 0.8em;
  margin-right: 5%;
  outline: none;
  cursor: pointer;
}
.btn_sure {
  color: rgb(255, 255, 255);
  border: 0;
  background-color: #eb854f;
}
.btn_sure:hover {
  background-color: #f59d6e;
}
.btn_warn {
  color: #000;
  background-color: rgb(255, 255, 255);
  border: 1px solid #ccc;
}
.btn_warn:hover {
  background-color: rgb(247, 213, 194);
}
.close_btn {
  position: absolute;
  z-index: 1;
  top: 0.3em;
  right: 0.3em;
  cursor: pointer;
  /* ÊåâÈíÆÊòØ‰∏™Â∞èÊ°Ü */
  width: 2em;
  height: 2em;
  border: 0;
  background-color: #fff;
  /* ÊääÊåâÈíÆÈáåÈù¢ÁöÑÂ≠óÊå§Âá∫ÂéªÂπ∂ÈöêËóè */
  text-indent: 10em;
  overflow: hidden;
}
.close_btn::after {
  position: absolute;
  z-index: 1;
  line-height: 0.5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-indent: 0;
  font-size: 2rem;
  content: '\00D7';
}
</style>
